import React, { Component } from "react";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import Checkbox from "@material-ui/core/Checkbox";
import { Button } from "@material-ui/core";
import { LocalForm } from "react-redux-form";
import TextField from "@material-ui/core/TextField";

function RenderMultipleText({ question, handleMultiplechange, questionIdx }) {
  return (
    <FormControl>
      {question.options.map((ans, i) => (
        <div key={i} className="col-12">
          <FormControlLabel
            control={
              <Checkbox
                onChange={(event) => {
                  handleMultiplechange(event);
                }}
                value={ans.id}
                name={questionIdx + "_" + question.id + "_" + i}
              />
            }
            label={ans.answer_text}
          />
        </div>
      ))}
    </FormControl>
  );
}

function RenderMultipleImg({ question, handleMultiplechange, questionIdx }) {
  return (
    <FormControl>
      {question.options.map((ans, i) => (
        <div key={i} className="col-12">
          <FormControlLabel
            control={
              <Checkbox
                onChange={handleMultiplechange}
                value={ans.id}
                name={questionIdx + "_" + question.id + "_" + i}
              />
            }
            label={<img src={ans.answer_image} alt="" width="150px" />}
          />
        </div>
      ))}
    </FormControl>
  );
}

function RenderSingleText({ question, handleSinglechange, questionIdx }) {
  return (
    <RadioGroup aria-label={question.id} onChange={handleSinglechange}>
      {question.options.map((ans, i) => {
        return (
          <div key={i} className="col-12">
            <FormControlLabel
              value={ans.id.toString()}
              key={question.id}
              name={questionIdx + "_" + question.id}
              control={<Radio />}
              label={ans.answer_text}
            />
          </div>
        );
      })}
    </RadioGroup>
  );
}

function RenderSingleImage({ question, handleSinglechange, questionIdx }) {
  return (
    <RadioGroup aria-label={question.id} onChange={handleSinglechange}>
      {question.options.map((ans, i) => {
        return (
          <div key={i} className="col-12">
            <FormControlLabel
              value={ans.id.toString()}
              name={questionIdx + "_" + question.id}
              control={<Radio />}
              label={<img src={ans.answer_image} alt="" width="150px" />}
            />
          </div>
        );
      })}
    </RadioGroup>
  );
}

function RenderInput({ question, handleTextInput, questionIdx }) {
  const [value, setValue] = React.useState("");

  const handleChange = (event) => {
    setValue(event.target.value);
    handleTextInput(event.target.name, event.target.value);
  };

  return (
    <div style={{ marginLeft: 20 }}>
      <TextField
        id="standard-basic"
        rowsMax={4}
        value={value}
        name={questionIdx + "_" + question.id}
        required
        onChange={handleChange}
      />
    </div>
  );
}

function RenderQuiz({
  quiz,
  handleSinglechange,
  handleMultiplechange,
  handleTextInput,
}) {
  return (
    <div className="quiz Container">
      {quiz.quiz.questions.map((question, i) => {
        return (
          <div key={i} style={{ marginTop: 20 }}>
            <h6>{`${i + 1}. ` + question.question}</h6>
            {question.image ? (
              <img src={question.image} alt="" width="200px" />
            ) : null}
            {question.multiple_choices ? (
              <div>
                {question.answer_type === "text" ? (
                  <RenderMultipleText
                    questionIdx={i}
                    question={question}
                    handleMultiplechange={handleMultiplechange}
                  />
                ) : question.answer_type === "image" ? (
                  <RenderMultipleImg
                    questionIdx={i}
                    question={question}
                    handleMultiplechange={handleMultiplechange}
                  />
                ) : (
                  <RenderInput
                    questionIdx={i}
                    question={question}
                    handleTextInput={handleTextInput}
                  />
                )}
              </div>
            ) : (
              <div>
                {question.answer_type === "text" ? (
                  <RenderSingleText
                    questionIdx={i}
                    question={question}
                    handleSinglechange={handleSinglechange}
                  />
                ) : question.answer_type === "image" ? (
                  <RenderSingleImage
                    questionIdx={i}
                    question={question}
                    handleSinglechange={handleSinglechange}
                  />
                ) : (
                  <RenderInput
                    questionIdx={i}
                    question={question}
                    handleTextInput={handleTextInput}
                  />
                )}
              </div>
            )}
          </div>
        );
      })}
    </div>
  );
}

