function RenderAns({ question, answers }) {
  if (question.answer_type === "text") {
    return question.options.map((option) => {
      if (answers[option.id] === true) {
        return (
          <div style={{ color: "green" }}>
            <h6>{option.answer_text}</h6>
          </div>
        );
      } else if (answers[option.id] === false) {
        return (
          <div style={{ color: "red" }}>
            <h6>{option.answer_text}</h6>
          </div>
        );
      } else {
        return null;
      }
    });
  } else if (question.answer_type === "image") {
    return question.options.map((option) => {
      if (answers[option.id] === true) {
        return (
          <div>
            <img src={option.answer_image} alt="" width="150px" />
          </div>
        );
      } else if (answers[option.id] === false) {
        return (
          <div>
            <img src={option.answer_image} alt="" width="150px" />
          </div>
        );
      } else {
        return null;
      }
    });
  } else {
    return null;
  }
}

function RenderQuestion({ questionIdx, question, response }) {
  return (
    <div>
      <div
        style={{
          display: "flex",
          flex: "row",
          color: response.submission[questionIdx].score === 1 ? "green" : "red",
        }}
      >
        <div>
          <h5>{`${questionIdx + 1}. ` + question.question}</h5>
        </div>
        <div style={{ marginLeft: "auto" }}>
          <h5>{response.submission[questionIdx].score}</h5>
        </div>
      </div>
      {question.image ? (
        <div>
          <img src={question.image} alt="" width="200px" />
        </div>
      ) : null}
      <div style={{ display: "flex", flex: "row" }}>
        <div>Ans:&nbsp;&nbsp;</div>
        <div>
          <RenderAns
            question={question}
            answers={response.submission[questionIdx].selected_answers}
          />
        </div>
      </div>
    </div>
  );
}

function RenderResponse({ quiz, response }) {
  return (
    <div className="quiz Container">
      <div style={{ backgroundColor: "#06bfad", color: "white", padding: 10 }}>
        <h6>Your score is {response.score}</h6>
      </div>
      {quiz.quiz.questions.map((question, i) => {
        return (
          <div key={i}>
            <div style={{ marginTop: 20 }}>
              <div className="answer">
                {
                  <RenderQuestion
                    question={question}
                    questionIdx={i}
                    response={response}
                  />
                }
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
}

class QuizComponent extends Component {
  constructor(props) {
    super(props);

    this.state = {
      response: new Array(this.props.courses.quizData.quiz.questions.length),
      answers: new Array(this.props.courses.quizData.quiz.questions.length),
      submitted: false,
      score: 0,
    };

    this.handleSinglechange = this.handleSinglechange.bind(this);
    this.handleMultiplechange = this.handleMultiplechange.bind(this);
    this.checkAns = this.checkAns.bind(this);
    this.handleTextInput = this.handleTextInput.bind(this);
    this.submitted = this.submitted.bind(this);
  }

  handleSinglechange(e) {
    let [questionIdx, questionId] = e.target.name.split("_");
    const ans = this.state.answers;
    let selected_answers = [Number(e.target.value)];
    ans[Number(questionIdx)] = {
      id: Number(questionId),
      selected_answers: selected_answers,
    };
    this.setState({
      answers: ans,
    });
  }

  checkSelected(element) {
    return typeof element != "boolean";
  }

  handleMultiplechange(e) {
    let [questionIdx, questionId, optionIdx] = e.target.name.split("_");
    const correct = this.state.response[Number(questionIdx)]
      ? this.state.response[Number(questionIdx)]
      : [false, false, false, false];
    if (e.target.checked) {
      correct[optionIdx] = [true, e.target.value];
    } else {
      correct[optionIdx] = false;
    }
    const res = this.state.response;
    res[Number(questionIdx)] = correct;
    this.setState({
      response: res,
    });
    let ansArray = [];
    this.state.response[Number(questionIdx)]
      .filter(this.checkSelected)
      .map((element) => {
        ansArray.push(Number(element[1]));
        return null;
      });
    let ans = this.state.answers;
    ans[Number(questionIdx)] = {
      id: Number(questionId),
      selected_answers: ansArray,
    };
    this.setState({
      answers: ans,
    });
  }

  handleTextInput(name, answer) {
    let [questionIdx, questionId] = name.split("_");
    const ans = this.state.answers;
    ans[Number(questionIdx)] = {
      id: Number(questionId),
      selected_answers: answer,
    };
    this.setState({
      answers: ans,
    });
  }

  submitted() {
    this.setState({
      submitted: true,
    });
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  }

  checkAns() {
    let submission = {
      id: Number(this.props.courses.quizData.id),
      answers: this.state.answers,
    };
    this.props.submitQuiz(submission, this.submitted);
  }

  render() {
    return (
      <div
        className="quiz container"
        style={{ marginTop: 60, marginBottom: 40, paddingLeft: 50 }}
      >
        <div className="row justify-content-center">
          <div className="col-12">
            <h4>{this.props.quiz.quiz.title}</h4>
            {!this.state.submitted ? (
              <LocalForm onSubmit={this.checkAns}>
                <RenderQuiz
                  handleSinglechange={this.handleSinglechange}
                  handleTextInput={this.handleTextInput}
                  handleMultiplechange={this.handleMultiplechange}
                  quiz={this.props.quiz}
                />
                <hr />
                <div className="row">
                  <Button
                    style={{
                      marginTop: 20,
                      backgroundColor: "#06bfad",
                      outline: "none",
                    }}
                    type="submit"
                    className="ml-auto"
                    variant="contained"
                  >
                    submit
                  </Button>
                </div>
              </LocalForm>
            ) : (
              <div>
                <RenderResponse
                  quiz={this.props.quiz}
                  response={this.props.courses.quizResponse}
                />
                <Button
                  style={{ marginTop: 20 }}
                  variant="contained"
                  onClick={() => {
                    this.props.next();
                    this.props.quizSubmission();
                  }}
                >
                  Next
                </Button>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default QuizComponent;
