import React, { Component } from "react";
import ReactPlayer from "react-player";
import "./Learn/Learn.css";
import PlayCircleFilledRoundedIcon from "@material-ui/icons/PlayCircleFilledRounded";

class Player extends Component {
  constructor(props) {
    super(props);
    this.state = {
      requested: false,
      done: false,
    };
    this.success = this.success.bind(this);
    this.failed = this.failed.bind(this);
  }

  success() {
    this.setState({
      done: true,
    });
  }

  failed() {
    this.setState({
      requested: false,
      done: false,
    });
  }

  render() {
    return (
      <ReactPlayer
        className="react-player"
        url={this.props.url}
        // url="https://www.youtube.com/watch?v=JskjyZzW6MA&t=44s"
        // url={`https://player.vimeo.com/video/${this.props.vimeoId}`}
        width="100%"
        height={window.innerWidth < 1003 ? "100%" : "100%"}
        controls={true}
        onEnded={this.props.next}
        // onEnded={() => this.props.contentDone(this.props.contentId, this.props.progress_id)}
        onProgress={(played) => {
          if (
            played.played > 0.88 &&
            !this.state.requested &&
            !this.state.done &&
            !this.props.is_done
          ) {
            this.props.contentDone(
              this.props.contentId,
              // this.props.progress_id,
              this.props.modIdx,
              this.props.cntIdx,
              this.success,
              this.failed
            );
            this.setState({
              requested: true,
            });
          }
        }}
        onReady={() => {
          this.setState({
            done: false,
            requested: false,
          });
        }}
        playing={false}
        light={true}
        playIcon={
          <PlayCircleFilledRoundedIcon
            style={{ color: "#06bfad", width: 50, height: 50 }}
          />
        }
      />
    );
  }
}

export default Player;
